
import { defineComponent } from "vue";
import "boxicons";
import { useToast } from "vue-toastification";
import {
  MDBModal,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
  MDBBtn,
  MDBContainer,
  MDBRow,
  MDBCol,
} from "mdb-vue-ui-kit";

export default defineComponent({
  components: {
    MDBModal,
    MDBModalHeader,
    MDBModalTitle,
    MDBModalBody,
    MDBModalFooter,
    MDBBtn,
    MDBContainer,
    MDBRow,
    MDBCol,
  },
  setup() {
    // Get toast interface
    const toast = useToast();
    return { toast };
  },
  data() {
    return {
      isActive: false,
      isGameActive: false,
      showControls: true,
      playerScore: 0,
      computerScore: 0,
      winCount: 5,

      rockVpaper: false,
      rockVrock: false,
      rockVscissor: false,

      paperVrock: false,
      paperVpaper: false,
      paperVscissor: false,

      scissorVrock: false,
      scissorVpaper: false,
      scissorVscissor: false,
    };
  },
  methods: {
    startFunction() {
      this.isActive = true;
    },
    startGame() {
      this.isActive = !this.isActive;
      this.isGameActive = !this.isGameActive;
    },
    getRandomInt(min: number, max: number) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min + 1)) + min;
    },
    determineWin(player: number, computer: number) {
      if (this.playerScore < 5) {
        if (this.computerScore < 5) {
          switch (player) {
            case 0:
              if (computer === 0) {
                this.rockVrock = true;
                this.showControls = false;
                setTimeout(() => {
                  this.toast.info(`TIE ROUND!`);
                  this.rockVrock = false;
                  this.showControls = true;
                }, 5000);
              } else {
                if (computer === 1) {
                  this.rockVpaper = true;
                  this.showControls = false;
                  setTimeout(() => {
                    this.toast.error(`ROUND LOST!`);
                    const val = this.computerScore;
                    this.computerScore = val + 1;
                    this.rockVpaper = false;
                    this.showControls = true;
                  }, 5000);
                } else {
                  if (computer === 2) {
                    this.rockVscissor = true;
                    this.showControls = false;
                    setTimeout(() => {
                      this.toast.success(`ROUND WIN!`);
                      const val = this.playerScore;
                      this.playerScore = val + 1;
                      this.rockVscissor = false;
                      this.showControls = true;
                    }, 5000);
                  }
                }
              }
              break;
            case 1:
              if (computer === 0) {
                this.paperVrock = true;
                this.showControls = false;
                setTimeout(() => {
                  this.toast.success(`ROUND WIN!`);
                  const val = this.playerScore;
                  this.playerScore = val + 1;
                  this.paperVrock = false;
                  this.showControls = true;
                }, 5000);
              } else {
                if (computer === 1) {
                  this.paperVpaper = true;
                  this.showControls = false;
                  setTimeout(() => {
                    this.toast.info(`TIE ROUND!`);
                    this.paperVpaper = false;
                    this.showControls = true;
                  }, 5000);
                } else {
                  if (computer === 2) {
                    this.paperVscissor = true;
                    this.showControls = false;
                    setTimeout(() => {
                      this.toast.error(`ROUND LOST!`);
                      const val = this.computerScore;
                      this.computerScore = val + 1;
                      this.paperVscissor = false;
                      this.showControls = true;
                    }, 5000);
                  }
                }
              }
              break;
            case 2:
              if (computer === 0) {
                this.scissorVrock = true;
                this.showControls = false;
                setTimeout(() => {
                  this.toast.error(`ROUND LOST!`);
                  const val = this.computerScore;
                  this.computerScore = val + 1;
                  this.scissorVrock = false;
                  this.showControls = true;
                }, 5000);
              } else {
                if (computer === 1) {
                  this.scissorVpaper = true;
                  this.showControls = false;
                  setTimeout(() => {
                    this.toast.success(`ROUND WIN!`);
                    const val = this.playerScore;
                    this.playerScore = val + 1;
                    this.scissorVpaper = false;
                    this.showControls = true;
                  }, 5000);
                } else {
                  if (computer === 2) {
                    this.scissorVscissor = true;
                    this.showControls = false;
                    setTimeout(() => {
                      this.toast.info(`TIE ROUND!`);
                      this.scissorVscissor = false;
                      this.showControls = true;
                    }, 5000);
                  }
                }
              }
              break;
          }
        } else {
          this.showControls = false;
        }
      } else {
        this.showControls = false;
      }
    },
    optionChoice(e: number) {
      const compChoice = this.getRandomInt(0, 3);
      console.log(compChoice);
      switch (e) {
        case 0:
          //ROCK
          this.determineWin(0, compChoice);
          break;
        case 1:
          //PAPER
          this.determineWin(1, compChoice);
          break;
        case 2:
          //SCISSORS
          this.determineWin(2, compChoice);
          break;
      }
    },
    restartGame() {
      this.playerScore = 0;
      this.computerScore = 0;
      this.isActive = true;
      this.isGameActive = false;
      this.showControls = true;
    },
  },
  mounted() {
    setInterval(() => {
      console.log(this.isActive, this.isGameActive, this.showControls);
    }, 5000);
    setTimeout(() => {
      this.startFunction();
    }, 2000);
  },
});
